import {useCallback, useEffect, useState} from "react";
import callApi from "../../../utils/apiCaller";

export interface IPassingState {
  isLoading: boolean;
  value: any;
}

export const Initial_Loading = {
  isLoading: true,
  value: null,
}

export const Initial_Non_Loading = {
  isLoading: false,
  value: null,
}

const useGetNFTDetails = (nftId: number) => {
  const [value, setValue] = useState<IPassingState>(
    Initial_Loading
  );

  const fetchData = useCallback(async () => {
    if (nftId !== 0) {
      const body = {
        "nft": [nftId]
      }
      const userNft = await callApi('getUserNft', 'post', body);

      const data = userNft === undefined? null: userNft[0];

      setValue({
        isLoading: false,
        value: data || null,
      })
    }
  }, [nftId]);

  useEffect(() => {
    setValue(Initial_Loading);
    fetchData().catch((err) => {
      setValue(Initial_Non_Loading);
      console.error(`Failed to fetch nft details: ${err.stack}`);
    });
  }, [nftId, fetchData]);

  return value;
};

export default useGetNFTDetails;
